





















































































.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0.51rem 0.5rem 0 0.5rem;
  .search-zone {
    position: relative;
    input::-ms-input-placeholder {
      text-align: center;
    }
    input::-webkit-input-placeholder {
      text-align: center;
    }
    .search-input {
      border: 1px solid #333;
      width: 100%;
      height: 0.8rem;
      font-size: 0.3rem;
      padding-left: 0.1rem;
    }
    .search-icon {
      position: absolute;
      top: 0;
      right: 0;
      color: red;
    }
  }
  .search-list {
    margin-top: 0.3rem;
    .score-item {
      display: inline-block;
      position: relative;
      overflow: hidden;
      margin-top: 0.1rem;
      background: yellow;
      height: 100%;
      line-height: 0.6rem;
      font-size: 0.3rem;
      width: 100%;
      background-color: #d6d2d2;
      border-radius: 0.05rem;
      .score-name {
        float: left;
        width: 80%;
        word-wrap: break-word;
        word-break: normal;
      }
      .score-store {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        right: 0;
        width: 20%;
        height: 100%;
        line-height: 0.6rem;
        font-size: 0.3rem;
        color: #fff;
        width: 1rem;
        background-color: orange;
      }
    }
  }
}
