.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 8.16px 8px 0 8px;
  padding: 0.51rem 0.5rem 0 0.5rem;
}
.container .search-zone {
  position: relative;
}
.container .search-zone input::-ms-input-placeholder {
  text-align: center;
}
.container .search-zone input::-webkit-input-placeholder {
  text-align: center;
}
.container .search-zone .search-input {
  border: 1px solid #333;
  width: 100%;
  height: 12.8px;
  height: 0.8rem;
  font-size: 4.8px;
  font-size: 0.3rem;
  padding-left: 1.6px;
  padding-left: 0.1rem;
}
.container .search-zone .search-icon {
  position: absolute;
  top: 0;
  right: 0;
  color: red;
}
.container .search-list {
  margin-top: 4.8px;
  margin-top: 0.3rem;
}
.container .search-list .score-item {
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin-top: 1.6px;
  margin-top: 0.1rem;
  background: yellow;
  height: 100%;
  line-height: 9.6px;
  line-height: 0.6rem;
  font-size: 4.8px;
  font-size: 0.3rem;
  width: 100%;
  background-color: #d6d2d2;
  border-radius: 0.05rem;
}
.container .search-list .score-item .score-name {
  float: left;
  width: 80%;
  word-wrap: break-word;
  word-break: normal;
}
.container .search-list .score-item .score-store {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  right: 0;
  width: 20%;
  height: 100%;
  line-height: 9.6px;
  line-height: 0.6rem;
  font-size: 4.8px;
  font-size: 0.3rem;
  color: #fff;
  width: 16px;
  width: 1rem;
  background-color: orange;
}
